<template>
  <form @submit.prevent="collectData">
    <div class="page-contact-us">
      <div class="page-contact-result-web">
        <h2 class="contct-us-overall">整体来看</h2>
        <AnalyzeResult :analyzeData='analyzeData'></AnalyzeResult>
        <AnalyzeTab :analyzeData='analyzeData'></AnalyzeTab>
      </div>
      <p class="contct-us-info-text">立即填写下列资料并完成送出，就可取得贵公司在「生产流程」、「及时上市」、「品质管理」…等完整八大维度深入分析与优化建议！</p>
      <div class="page-contact-box-web">
        <p class="region-label">基本资料</p>
        <div class="inline-input-block">
          <div class="input-block">
            <input type="text"
              name="contactName"
              placeholder="名称"
              v-validate="'required'"
              v-model="contactData.CONTACT_NAME"
            >
            <div class="error-msg"
              v-show="errors.has('contactName')"
            >{{ errors.first('contactName') }}</div>
          </div>
          <div class="input-block">
            <input type="text"
              name="contactTitle"
              placeholder="职称"
              v-validate="'required'"
              v-model="contactData.CONTACT_TITLE"
            >
            <div class="error-msg"
              v-show="errors.has('contactTitle')"
            >{{ errors.first('contactTitle') }}</div>
          </div>
        </div>
        <div class="input-block">
          <input type="text"
            name="mobilePhone"
            placeholder="连络电话"
            v-validate="'required'"
            v-model="contactData.MOBILE_PHONE"
          >
          <div class="error-msg"
            v-show="errors.has('mobilePhone')"
          >{{ errors.first('mobilePhone') }}</div>
        </div>
        <p clas="region-label">公司资料</p>
        <div class="input-block">
          <input type="text"
            name="comanyName"
            placeholder="公司名称"
            v-validate="'required'"
            v-model="contactData.COMPANY_NAME"
          >
          <div class="error-msg"
            v-show="errors.has('comanyName')"
          >{{ errors.first('comanyName') }}</div>
        </div>
        <div class="input-block">
          <input type="text"
            name="companyPhone"
            placeholder="公司连络电话"
            v-validate="'required'"
            v-model="contactData.COMPANY_PHONE"
          >
          <div class="error-msg"
            v-show="errors.has('companyPhone')"
          >{{ errors.first('companyPhone') }}</div>
        </div>
        <p class="region-label">认证信箱</p>
        <div class="input-block">
          <input
            name="companyEmail"
            type="email"
            v-validate="'required|email'"
            placeholder="請輸入公司信箱 (不接受免費信箱，例如.gmail, yahoo等)"
            v-model="contactData.COMPANY_EMAIL"
            @click.once="popupFreeMail"
          >
          <div class="error-msg"
            v-show="errors.has('companyEmail')"
          >此信箱类型无法进行验证，详细内容请查看无法认证信箱类型</div>
        </div>
        <div class="inline-input-block contact-us-verify">
          <div class="contact-us-send-verify-box">
            <button v-ga="$ga.commands.trackEvent.bind(this, '/form1sch', 'S-verify_click')" type="button" v-if="!codeStatus.sendMsgDisabled && !codeStatus.reGet" @click.prevent="sendValidateCode" class="contact-us-send-verify">发送验证码</button>
            <button type="button" v-if="!codeStatus.sendMsgDisabled && codeStatus.reGet" @click.prevent="sendValidateCode" class="contact-us-send-verify-gray">发送验证码（60s）</button>
            <button style='cursor: text;' type="button" v-if="codeStatus.sendMsgDisabled" class="contact-us-send-verify-gray">{{'重新发送（'+ codeStatus.rTime + 's）'}}</button>
          </div>
          <div class="contact-us-input-mail-code-box input-block">
            <input type="text"
              name="VerCode"
              class="contact-us-input-mail-code"
              placeholder="信箱验证码"
              v-validate="'required'"
              v-model="verifyCode.typeCode"
            >
            <div class="error-msg"
              v-show="errors.has('VerCode')"
            >{{ errors.first('VerCode') }}</div>
          </div>
        </div>
        <button v-ga="$ga.commands.trackEvent.bind(this, '/form1sch', 'S-send_click')" type="submit" class="submit-button" :class="{formActive: sendForm}">送出表单</button>
      </div>
    </div>
  </form>
</template>

<script>

import AnalyzeResult from '@/components/analyze/AnalyzeResult'
import AnalyzeTab from '@/components/analyze/AnalyzeTab'
import { sendCompanyData, getComputeAnalyzeMain, getEmailCheckVerCode } from '@/API/analyze'

export default {
  inject: ['$validator'],
  name: 'ContactUs',
  components: {
    AnalyzeResult,
    AnalyzeTab
  },
  data () {
    return {
      contactData: {
        QUEST_REPORT: '',
        COMPANY_NO: '',
        CONTACT_NAME: '',
        CONTACT_TITLE: '',
        MOBILE_PHONE: '',
        COMPANY_NAME: '',
        COMPANY_PHONE: '',
        COMPANY_EMAIL: '',
        EMAIL_CHECK: '',
        CITY_CODE: '',
        CATEGORY_CODE: '',
        CONTACT_TIME: '',
        IMPORT_SYSTEM_TIME: '',
        BUDGET: '',
        REMARK: ''
      },
      verifyCode: {
        VerCode: 'empty',
        typeCode: ''
      },
      codeStatus: {
        reGet: false,
        rTime: 60,
        sendMsgDisabled: false
      },
      sendForm: false,
      analyzeData: {}
    }
  },
  mounted () {
    window.scrollTo(0, 0)
    for (let i in this.clientData) {
      if (this.clientData[i]) {
        this.contactData[i] = this.clientData[i]
      }
    }
    this.contactData.COMPANY_NO = this.companyNum
    this.contactData.QUEST_REPORT = this.responseList
    this.contactData.CITY_CODE = this.responseList[0].Q027
    this.contactData.CATEGORY_CODE = this.responseList[1].Q028
    // getComputeAnalyzeMain({
    //   'COMPANY_NO': this.companyNum,
    //   'LANGUAGE': this.language
    // })
    //   .then(response => {
        let response = {
          data: {}
        }
        response.data = {"sameLevel":46,"chartData":[8,46,38,8,0],"level":2,"winLevel":8,"loseLevel":46,"industry":"烟草制品业"}
        this.analyzeData = response.data
      // })
      // .catch(err => {
      //   console.log(err)
      // })
  },
  watch: {
    contactData: {
      deep: true,
      handler (newValue, oldValue) {
        for (let i in this.contactData) {
          if (this.contactData[i] || i === 'IMPORT_SYSTEM_TIME' || i === 'BUDGET' || i === 'REMARK' || i === 'CITY_CODE' || i === 'CATEGORY_CODE' || i === 'QUEST_REPORT' || i === 'CONTACT_TIME') {

          } else {
            this.sendForm = false
            return
          }
          this.sendForm = true
        }
      }
    },
    verifyCode: {
      deep: true,
      handler (newValue, oldValue) {
        if (newValue.VerCode === newValue.typeCode) {
          this.contactData.EMAIL_CHECK = 'Y'
        } else {
          this.contactData.EMAIL_CHECK = ''
        }
      }
    }
  },
  computed: {
    responseList () {
      return this.$store.state.responseList
    },
    language () {
      return this.$store.state.language
    },
    companyNum () {
      return this.$store.state.companyNum
    },
    clientData () {
      return this.$store.state.clientData
    }
  },
  methods: {
    collectData () {
      this.contactData.QUEST_REPORT = this.responseList
      this.contactData.CITY_CODE = this.responseList[0].Q027
      this.contactData.CATEGORY_CODE = this.responseList[1].Q028
      // 先驗證 verifyCode.Vercode
      if (this.verifyCode.VerCode === this.verifyCode.typeCode) {
        this.contactData.EMAIL_CHECK = 'Y'
      } else {
        alert('认证码错误')
        return
      }
      // 發送 contactData
      this.$validator.validateAll().then(result => {
        this.$store.commit('setClientData', this.contactData)
        if (result) {
          this.$router.push({path: '/result'})
          // sendCompanyData(
          //   this.contactData
          // )
          //   .then(response => {
          //     // console.log(response)
          //     this.$router.push({path: '/result'})
          //   })
          //   .catch(err => {
          //     console.log(err)
          //   })
        }
      })
    },
    sendValidateCode () {
      this.$validator.validate('companyEmail', this.contactData.COMPANY_EMAIL).then(result => {
        getEmailCheckVerCode({
          COMPANY_EMAIL: this.contactData.COMPANY_EMAIL
        })
          .then(response => {
            this.verifyCode.VerCode = response.data.VerCode
          })
          .catch(err => {
            console.log(err)
          })
        if (result) {
          this.codeStatus.sendMsgDisabled = true
          let rTime = this.codeStatus.rTime
          // 倒數
          let interval = window.setInterval(() => {
            if (--this.codeStatus.rTime <= 0) {
              this.codeStatus.rTime = rTime
              this.codeStatus.sendMsgDisabled = false
              this.codeStatus.reGet = true
              window.clearInterval(interval)
            }
          }, 1000)
        }
      })
    },
    popupFreeMail () {
      alert('请填入公司E-mail，以避免漏接验证码。')
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.page-contact-us {
  height: 100%;
  width: 90%;
  margin: 0 auto;
  background-color: $theme-color-black;
  color: $theme-color-white;

  .contct-us-overall{
    padding-bottom: 20px;
    text-align: center;
  }

  .contct-us-info-text{
    padding-bottom: 40px;
    color: $theme-color-gray;
  }

  .region-label {
    margin-bottom: 16px;
  }

  .contact-us-verify {
    margin-bottom: 40px;

    .contact-us-send-verify-box {
      width: 30%;
      margin-right: 5%;
    }

    .contact-us-send-verify {
      width: 100%;
      background: $theme-color-black;
      border: 1px solid $theme-color-primary;
      color: $theme-color-primary;
      padding: 6px 0px;
      cursor: pointer;
    }

    .contact-us-send-verify-gray {
      width: 100%;
      background: transparent;
      border: 1px solid #797D87;
      color: #797D87;;
      padding: 6px 0px;
      cursor: pointer;
    }

    .contact-us-input-mail-code-box {
      width: 65%;
    }
  }
}

.submit-button {
  width: 100%;
  height: 49px;
  border: none;
  color: white;
  background-color: #A3A6AD;
  opacity: 0.47;
  cursor: text;
}

.formActive{
  background: linear-gradient(90deg, #0BB5F7 0%, #06DBC3 100%);
  color: $theme-color-white;
  cursor: pointer;
  opacity: 1;
}

@media( min-width: 767px ){
  .page-contact-result-web{
    width: 400px;
    margin: auto;
    padding-bottom: 40px;
  }

  .contct-us-info-text{
    padding-top: 40px;
    width: 580px;
    margin: auto;
  }

  .page-contact-box-web{
    width: 510px;
    margin: auto;
  }
}

</style>



