<template>
    <div class="analyze-tab-box">
        <div class="analyze-tab">
            <div v-if="analyzeData.loseLevel === 100" class="analyze-tab-only-btn">{{levels[0].text}}</div>
            <div v-else-if="analyzeData.sameLevel === 100" class="analyze-tab-only-btn">{{levels[1].text}}</div>
            <div v-else-if="analyzeData.winLevel === 100" class="analyze-tab-only-btn">{{levels[2].text}}</div>
            <div
              v-else
              v-for="(level, index) in levels"
              :key="index"
              class="analyze-tab-btn"
              :class="{btnActived: tabSelect === level.type}"
              @click="selectTab(level.type)"
            >{{level.text}}</div>
        </div>
        <template v-if="tabSelect === 'lose'">
            <div v-if="analyzeData.loseLevel !== 0 && analyzeData.loseLevel !== 100" class="analyze-tab-text">
                另外有<span class="analyze-tab-key-word">{{analyzeData.loseLevel}}%</span>的同行的工业4.0程度在贵司之上
            </div>
            <div v-if="analyzeData.loseLevel === 0" class="analyze-tab-text">{{text.winScore}}</div>
            <div v-if="analyzeData.loseLevel === 100" class="analyze-tab-text">{{text.loseScore}}</div>
        </template>
        <template v-if="tabSelect === 'same'">
            <div v-if="analyzeData.sameLevel !== 0 && analyzeData.sameLevel !== 100" class="analyze-tab-text">
                有<span class="analyze-tab-key-word">{{analyzeData.sameLevel}}%</span>的同行跟您在同一个程度
            </div>
            <div v-if="analyzeData.sameLevel === 0" class="analyze-tab-text">{{text.noSameScore}}</div>
            <div v-if="analyzeData.sameLevel === 100" class="analyze-tab-text">{{text.sameScore}}</div>
        </template>
        <template v-if="tabSelect === 'win'">
            <div v-if="analyzeData.winLevel !== 0 && analyzeData.winLevel !== 100" class="analyze-tab-text">
                貴司領先了<span class="analyze-tab-key-word">{{analyzeData.winLevel}}%</span>的同行
            </div>
            <div v-if="analyzeData.winLevel === 0" class="analyze-tab-text">{{text.loseScore}}</div>
            <div v-if="analyzeData.winLevel === 100" class="analyze-tab-text">{{text.winScore}}</div>
        </template>
        <div class="analyze-tab-line">
            <div :style="{ width: analyzeData.loseLevel * 100 + '%' }" :class="loseLineObject"></div>
            <div :style="{ width: analyzeData.sameLevel * 100 + '%' }" :class="sameLineObject"></div>
            <div :style="{ width: analyzeData.winLevel * 100 + '%' }" :class="winLineObject"></div>
        </div>
    </div>
</template>

<script>

export default {
  name: 'AnalyzeTab',
  props: {
    analyzeData: { type: Object, default: () => ({}) }
  },
  data () {
    return {
      tabSelect: 'lose',
      levels: [
        { type: 'lose', status: this.analyzeData.loseLevel, text: '在您之上' },
        { type: 'same', status: this.analyzeData.sameLevel, text: '贵公司程度' },
        { type: 'win', status: this.analyzeData.winLevel, text: '在您之下' }
      ],
      text: {
        loseScore: '您在产业中大幅落后！请让我们协助您！',
        winScore: '恭喜您，您的工业4.0程度是同行中的佼佼者！',
        sameScore: '您与同行在一个程度！',
        noSameScore: '尚未有同行与您在同一个程度'
      }
    }
  },
  methods: {
    selectTab (status) {
      this.tabSelect = status
    }
  },
  computed: {
    loseLineObject: function () {
      return {
        'analyze-tab-gray': true,
        primaryColor: this.tabSelect === 'lose',
        thinGray: this.tabSelect === 'win'
      }
    },
    sameLineObject: function () {
      return {
        'analyze-tab-gray': true,
        primaryColor: this.tabSelect === 'same',
        thinGray: this.tabSelect === 'lose'
      }
    },
    winLineObject: function () {
      return {
        'analyze-tab-gray': true,
        primaryColor: this.tabSelect === 'win',
        thinGray: this.tabSelect === 'same'
      }
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    .analyze-tab-box{
      border: 1px solid rgba(255, 255, 255, 0.48);
      margin-bottom: 20px;

      .analyze-tab{
        display: flex;
        justify-content: space-evenly;
        margin-bottom: 40px;
      }

      .analyze-tab-btn{
        width: 100%;
        height: 30px;
        text-align: center;
        color: $theme-color-white;
        font-size: $theme-font-size-x-large;
        background: $theme-backbround-color-thin-gray;
        line-height: 30px;
        cursor: pointer;
      }

      .analyze-tab-only-btn{
        width: 100%;
        height: 30px;
        text-align: center;
        color: $theme-color-black;
        background: $theme-color-primary;
        line-height: 30px;
        cursor: text;
      }

      .btnActived{
        color: $theme-color-black;
        background: $theme-color-primary;
        cursor: text;
      }

      .analyze-tab-text{
        padding: 0px 10px 10px;
      }

      .analyze-tab-key-word{
        font-size: $theme-font-size-xxx-huge;
        color: $theme-color-primary;
      }

      .analyze-tab-line{
        width: 100%;
        height: 8px;
        display: flex;
        padding: 0px 10px 30px;
      }

      .analyze-tab-thin-gray{
        width: 33.3%;
        height: 8px;
        background: $theme-color-light-gray;
      }

      .analyze-tab-primary-green{
        width: 33.3%;
        height: 8px;
        background: $theme-color-primary;
      }

      .analyze-tab-gray{
        width: 33.3%;
        height: 8px;
        background: $theme-backbround-color-heavy-gray;
      }

      .primaryColor{
        background: $theme-color-primary;
      }

      .thinGray{
        background: $theme-color-light-gray;
      }
    }
</style>



