<template>
    <div class="analyze-background">
        <div class="analyze-feedback-text-box">
            <div class="analyze-feedback-text">在<span class="result-feedback-theme-color">{{analyzeData.industry}}</span>的比較裡，贵企业在工业4.0程度的落点分析为</div>
            <div class="analyze-feedback-number"><span class="result-feedback-theme-color result-feedback-medium-size">{{analyzeData.level}}</span>/ 5分</div>
        </div>
    </div>
</template>

<script>

export default {
  name: 'AnalyzeResult',
  props: {
    analyzeData: { type: Object, default: () => ({}) }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    .analyze-background{
        .analyze-feedback-text-box{
            margin-bottom: 25px;
            text-align: center;
        }

        .analyze-feedback-text{
          margin-bottom: 16px;
        }

        .analyze-feedback-number{
            height: 56px;
            font-size: $theme-font-size-large;
            text-align: center;
            color: $theme-color-white;
        }

        .analyze-feedback-list{
            width: 100%;
            height: 120px;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            background: rgba(255, 255, 255, 0.24);
            padding: 10px;
            margin: 0px auto 25px;
        }

        .result-feedback-theme-color{
          color: $theme-color-primary;
        }
        .result-feedback-medium-size{
            font-size: $theme-font-size-great;
            padding-left: 40px;
        }
    }
</style>



